<template>
  <div>
    <v-simple-table v-if="this.resultados.length > 0" id="jornada">
      <template v-slot:default>
        <caption>
          Jornada
          {{
            currentPage
          }}
        </caption>
        <v-simple-table
          v-for="(resultado, index) in resultados"
          v-bind:key="index"
          v-if="index >= startIndex && index < endIndex"
        >
          <caption class="horario">
            {{
              resultado.fixture.date.substring(0, 16).replace("T", " ")
            }}
          </caption>
          <tbody>
            <router-link :to="'/partido/' + resultado.fixture.id" tag="tr">
              <td class="local">
                <router-link :to="'/equipo/' + resultado.teams.home.id" tag="a">
                  {{ resultado.teams.home.name }}
                  <img
                    style="vertical-align: middle"
                    :src="resultado.teams.home.logo"
                  />
                </router-link>
              </td>
              <td class="goles-local">
                {{ resultado.goals.home }}
              </td>
              <td class="barra">-</td>
              <td class="goles-visitante">
                {{ resultado.goals.away }}
              </td>
              <td class="visitante">
                <router-link :to="'/equipo/' + resultado.teams.away.id" tag="a">
                  <img
                    style="vertical-align: middle"
                    :src="resultado.teams.away.logo"
                  />
                  {{ resultado.teams.away.name }}
                </router-link>
              </td>
            </router-link>
          </tbody>
        </v-simple-table>
      </template>
    </v-simple-table>
    <div class="buttons">
      <v-btn @click="previous()" rounded small class="ma-2"
        ><v-icon>mdi-chevron-left</v-icon></v-btn
      >
      <v-btn @click="next()" rounded small class="ma-2"
        ><v-icon>mdi-chevron-right</v-icon></v-btn
      >
    </div>
  </div>
</template>
<script>
export default {
  name: "Resultados",
  data() {
    return {
      resultados: [],
      perPage: null,
      currentPage: 1,
      startIndex: 0,
      endIndex: null,
    };
  },
  watch: {
    $route(to, from) {
      this.loadJornadas();
      this.loadResultados();
      this.currentPage = 1;
    },
  },
  created() {
    this.loadJornadas();
  },
  mounted() {
    this.loadResultados();
  },
  computed: {
    totalPages() {
      return Math.ceil(this.resultados.length / this.perPage);
    },
  },
  methods: {
    pagination: function (activePage) {
      this.currentPage = activePage;
      this.startIndex = this.currentPage * this.perPage - this.perPage;
      this.endIndex = this.startIndex + this.perPage;
    },
    countResultados: function () {
      var countResultados = 0;
      for (var index = 0; index < this.resultados.length; index++) {
        countResultados++;
      }
      return countResultados;
    },
    previous: function () {
      if (this.currentPage > 1) {
        this.pagination(this.currentPage - 1);
      }
    },
    next: function () {
      if (this.currentPage < this.totalPages) {
        this.pagination(this.currentPage + 1);
      }
    },
    loadResultados: function () {
      var axios = require("axios").default;

      var options = {
        method: "GET",
        url: "https://v3.football.api-sports.io/fixtures",
        params: {
          league: this.$route.params.idLiga,
          season: new Date().getFullYear(),
        },
        headers: {
          "x-rapidapi-host": "v3.football.api-sports.io",
          "x-rapidapi-key": "4ea797df57097c4612ae1e9ebb94554d",
        },
      };

      axios(options)
        .then((response) => {
          this.resultados = response.data.response.sort(
            (a, b) =>
              a.fixture.date - b.fixture.date ||
              a.fixture.date.localeCompare(b.fixture.date)
          );
          this.perPage = this.resultados.length / this.jornadas.length;
          this.endIndex = this.perPage;
        })
        .catch((error) => {
          console.log(error);
        });

      this.resultadosTotales = this.resultados.length;
    },
    loadJornadas: function () {
      var axios = require("axios").default;

      var options = {
        method: "GET",
        url: "https://v3.football.api-sports.io/fixtures/rounds",
        params: {
          league: this.$route.params.idLiga,
          season: new Date().getFullYear(),
        },
        headers: {
          "x-rapidapi-host": "v3.football.api-sports.io",
          "x-rapidapi-key": "4ea797df57097c4612ae1e9ebb94554d",
        },
      };

      axios(options)
        .then((response) => {
          this.jornadas = response.data.response;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
<style scoped>
a {
  text-decoration: none;
}

img {
  height: 40px;
}

td {
  font-size: 20px;
}

caption {
  margin: 1em;
}

a:link,
a:visited,
a:active {
  color: black;
}

a:hover {
  color: rgb(75, 130, 233);
}

.buttons {
  display: flex;
  justify-content: center;
}

.local {
  width: 180px;
  text-align: right;
}

.goles-local {
  font-size: 20px;
  font-weight: bold;
  width: 20px;
  text-align: right;
}

.goles-visitante {
  font-size: 20px;
  font-weight: bold;
  width: 20px;
}

.barra {
  width: 10px;
  text-align: center;
}

.visitante {
  width: 180px;
  text-align: left;
}

caption {
  font-size: 20px;
  font-weight: bold;
}

.horario {
  font-size: 15px;
}
</style>