<template>
  <v-app>
    <v-app-bar app color="primary" dark>
      <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
      <v-btn color="primary" to="/">
        <v-icon large>mdi-soccer</v-icon>
        <v-toolbar-title>FootballStatics</v-toolbar-title>
      </v-btn>
    </v-app-bar>
    <v-navigation-drawer
      v-model="drawer"
      absolute
      temporary
      class="mx-auto"
      width="400"
    >
      <v-list>
        <v-subheader color="primary">FootballStatics</v-subheader>
        <v-list-group prepend-icon="mdi-podium">
          <template v-slot:activator>
            <v-list-item-title>Competiciones</v-list-item-title>
          </template>

          <v-list-group no-action sub-group>
            <template v-slot:activator>
              <v-list-item-content>
                <v-list-item-title>Ligas</v-list-item-title>
              </v-list-item-content>
            </template>
            <v-list-item
              v-bind:to="'/liga/' + liga.league.id"
              v-for="liga in ligas"
              v-bind:key="liga.league.id"
            >
              <v-list-item-title>{{ liga.league.name }}</v-list-item-title>
              <v-list-item-icon>
                <img :src="liga.league.logo" height="20" />
              </v-list-item-icon>
            </v-list-item>
          </v-list-group>

          <v-list-group no-action sub-group>
            <template v-slot:activator>
              <v-list-item-content>
                <v-list-item-title>Copas</v-list-item-title>
              </v-list-item-content>
            </template>
            <v-list-item
              :to="'/copa/' + copa.league.id"
              v-for="copa in copas"
              v-bind:key="copa.league.id"
            >
              <v-list-item-title>{{ copa.league.name }}</v-list-item-title>
              <v-list-item-icon>
                <img :src="copa.league.logo" height="20" />
              </v-list-item-icon>
            </v-list-item>
          </v-list-group>
        </v-list-group>
        <v-list-group prepend-icon="mdi-tshirt-crew">
          <template v-slot:activator>
            <v-list-item-title>Equipos</v-list-item-title>
          </template>
        </v-list-group>
      </v-list>
    </v-navigation-drawer>
    <v-main>
      <router-view />
    </v-main>
    <v-footer color="primary lighten-1" padless>
      <v-row justify="center" no-gutters>
        <v-btn
          v-for="(icon, index) in icons"
          :href="icon.url"
          :key="index"
          class="mx-4"
          dark
          icon
        >
          <v-icon size="24px">
            {{ icon.icono }}
          </v-icon>
        </v-btn>
        <v-col class="primary py-4 text-center white--text" cols="12">
          {{ new Date().getFullYear() }} — <strong>FootballStatics</strong>
        </v-col>
      </v-row>
    </v-footer>
  </v-app>
</template>

<script>

export default {
  name: "App",
  data: () => ({
    drawer: false,
    ligas: {},
    copas: {},
    icons: [
      { icono: "mdi-facebook", url: "https://www.facebook.com/" },
      { icono: "mdi-twitter", url: "https://www.twitter.com/" },
      { icono: "mdi-linkedin", url: "https://www.linkedin.com/" },
      { icono: "mdi-instagram", url: "https://www.instagram.com/" },
    ],
  }),

  watch: {
    group() {
      this.drawer = false;
    },
  },
  mounted() {
    var axios = require("axios").default;

    var options = {
      method: "GET",
      url: "https://v3.football.api-sports.io/leagues",
      params: {
        country: "Spain",
        type: "cup",
        season: "2021",
        current: "true",
      },
      headers: {
        "x-rapidapi-host": "v3.football.api-sports.io",
        "x-rapidapi-key": "4ea797df57097c4612ae1e9ebb94554d",
      },
    };

    axios
      .request(options)
      .then((response) => {
        this.copas = response.data.response;
      })
      .catch(function (error) {
        console.error(error);
      });

    options.params.type = "league";

    axios
      .request(options)
      .then((response) => {
        this.ligas = response.data.response.sort(
          (a, b) =>
            a.league.id - b.league.id ||
            a.league.name.localeCompare(b.league.name)
        );
      })
      .catch(function (error) {
        console.error(error);
      });
  },
};
</script>
