<template>
  <div>
    <v-simple-table :search="search">
      <template v-slot:default>
        <thead>
          <tr>
            <th v-for="item in cabecera" v-bind:key="item">
              {{ item }}
            </th>
          </tr>
        </thead>
        <tbody
          v-for="(asistente, key) in asistentes"
          v-bind:key="asistente.player.id"
        >
          <tr>
            <td>
              {{ key + 1 }}
            </td>
            <td class="td-jugador">
              <router-link :to="'/jugador/' + asistente.player.id" tag="a">
                <img class="jugador"
                  :src="asistente.player.photo"
                  height="60"
                />
                {{ asistente.player.name }}
              </router-link>
            </td>
            <router-link
              :to="'/equipo/' + asistente.statistics[0].team.id"
              tag="td"
            >
              <img class="equipo" :src="asistente.statistics[0].team.logo" height="40" />
            </router-link>
            <td>
              {{ asistente.statistics[0].goals.assists }} ({{
                asistente.statistics[0].goals.total
              }})
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      asistentes: [],
      cabecera: ["Pos", "Jugador", "Equipo", "A"],
    };
  },
  computed: {
    search() {
      var axios = require("axios").default;

      var options = {
        method: "GET",
        url: "https://v3.football.api-sports.io/players/topassists",
        params: { season: "2021", league: this.$route.params.idLiga },
        headers: {
          "x-rapidapi-host": "v3.football.api-sports.io",
          "x-rapidapi-key": "4ea797df57097c4612ae1e9ebb94554d",
        },
      };

      axios(options)
        .then((response) => {
          this.asistentes = response.data.response;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
<style scoped>
.jugador {
  vertical-align: middle;
  border-radius: 20px;
  margin: 1em;
}

.equipo {
  margin: 1em;
  vertical-align: middle;
}

a {
  text-decoration: none;
}

a:link,
a:visited,
a:active {
  color: black;
}

a:hover {
  color: rgb(75, 130, 233);
}

@media (max-width: 450px) {
  .jugador {
    margin: 0;
    height: 40px;
  }

  img {
    height: 30px;
  }

  * {
    font-size: 11px;
  }
}
</style>