<template>
  <div class="container">
    <img class="jugador" :src="jugador.player.photo" alt="" />
    <div>
      <h1>{{ jugador.player.name }}</h1>
      <p>
        Edad: {{ jugador.player.age }} ({{
          jugador.player.birth.date | formatFecha
        }})
      </p>
      <p>Estatura: {{ jugador.player.height }}</p>
      <p>Peso: {{ jugador.player.weight }}</p>
      <div class="nacionalidad">
        <p>Nacionalidad:</p>
        <img :src="pais" alt="" />
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "InfoJugador",
  props: ["jugador"],
  filters: {
    formatFecha(fecha) {
      fecha = new Date(fecha);
      return fecha.toLocaleDateString();
    },
  },
  data() {
    return {
      pais: null,
    };
  },
  mounted() {
    var axios = require("axios").default;

    var getPais = {
      method: "GET",
      url: "https://v3.football.api-sports.io/countries",
      params: {
        name: this.jugador.player.nationality,
      },
      headers: {
        "x-rapidapi-host": "v3.football.api-sports.io",
        "x-rapidapi-key": "4ea797df57097c4612ae1e9ebb94554d",
      },
    };
    axios(getPais)
      .then((response) => {
        this.isLoaded = true;
        this.pais = response.data.response[0].flag;
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
</script>
<style scoped>
.container {
  display: flex;
  align-items: center;
  justify-content: left;
  margin: 0;
}

.jugador {
  height: 120px;
  border-radius: 20px;
  margin-right: .5em;
}

h1 {
  font-size: 25px;
}

p {
  margin: 0;
}

.nacionalidad {
  display: flex;
  align-items: center;
}

.nacionalidad img {
  height: 15px;
  margin-left: 5px;
}
</style>