<template>
  <v-card>
    <DatosEquipo v-bind:equipo="equipo"></DatosEquipo>
    <v-toolbar color="primary" dark height="40px">
      <v-tabs v-model="tabs" align-with-title>
        <v-tabs-slider color="white"></v-tabs-slider>
        <v-tab v-for="item in items" :key="item">
          {{ item }}
        </v-tab>
      </v-tabs>
    </v-toolbar>
    <v-tabs-items v-model="tabs">
      <v-tab-item v-if="isLoaded">
        <Plantilla v-bind:plantilla="[plantilla]"></Plantilla>
      </v-tab-item>
      <v-tab-item v-if="isLoaded">
        <!-- <Alineacion v-bind:partido="partido"></Alineacion> -->
      </v-tab-item>
    </v-tabs-items>
  </v-card>
</template>
<script>
import DatosEquipo from '../components/DatosEquipo.vue'
import Plantilla from '../components/Plantilla.vue'
export default {
  components : {DatosEquipo, Plantilla},
  name: "Equipo",
  data() {
    return {
      tabs: null,
      isLoaded : false,
      items: ["Plantilla"],
      plantilla: {
        porteros: [],
        defensas: [],
        centrocampistas: [],
        delanteros: []
      },
      equipo: []
    };
  },
  mounted() {
    var axios = require("axios").default;
    var getPlantilla = {
      method: "GET",
      url: "https://v3.football.api-sports.io/players/squads",
      params: {
        team: this.$route.params.idEquipo,
      },
      headers: {
        "x-rapidapi-host": "v3.football.api-sports.io",
        "x-rapidapi-key": "4ea797df57097c4612ae1e9ebb94554d",
      },
    };
    axios(getPlantilla)
      .then((response) => {
        this.isLoaded = true
        response.data.response[0].players.forEach(jugador => {
          switch (jugador.position) {
            case "Goalkeeper":
              this.plantilla.porteros.push(jugador)
              break;
            case "Defender":
              this.plantilla.defensas.push(jugador)
              break;
            case "Midfielder":
              this.plantilla.centrocampistas.push(jugador)
              break;
            case "Attacker":
              this.plantilla.delanteros.push(jugador)
              break;
            default:
              break;
          }
        });
        console.log(this.plantilla)
      })
      .catch((error) => {
        console.log(error);
      });
    
    var getEquipo = {
        method: "GET",
      url: "https://v3.football.api-sports.io/teams",
      params: {
        id: this.$route.params.idEquipo,
      },
      headers: {
        "x-rapidapi-host": "v3.football.api-sports.io",
        "x-rapidapi-key": "4ea797df57097c4612ae1e9ebb94554d",
      },
    };
    axios(getEquipo)
      .then((response) => {
        this.isLoaded = true
        this.equipo = response.data.response[0];
        console.log(response.data.response[0])
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
</script>