<template>
  <v-card v-if="isLoaded">
    <InfoJugador v-bind:jugador="jugador"></InfoJugador>
    <v-toolbar color="primary" dark height="40px">
      <v-tabs v-model="tabs" align-with-title>
        <v-tabs-slider color="white"></v-tabs-slider>
        <v-tab v-for="item in items" :key="item">
          {{ item }}
        </v-tab>
      </v-tabs>
    </v-toolbar>
    <v-tabs-items v-model="tabs">
      <v-tab-item v-if="isLoaded"> <StatsJugador></StatsJugador></v-tab-item>
    </v-tabs-items>
  </v-card>
</template>
<script>
import InfoJugador from "../components/InfoJugador.vue";
import StatsJugador from "../components/StatsJugador.vue";
export default {
  components: { InfoJugador, StatsJugador },
  name: "Jugador",
  data() {
    return {
      tabs: null,
      isLoaded: false,
      items: ["Estadísticas"],
      jugador: [],
    };
  },
  created() {
    var axios = require("axios").default;
    var getJugador = {
      method: "GET",
      url: "https://v3.football.api-sports.io/players",
      params: {
        id: this.$route.params.idJugador,
        season: new Date().getFullYear(),
      },
      headers: {
        "x-rapidapi-host": "v3.football.api-sports.io",
        "x-rapidapi-key": "4ea797df57097c4612ae1e9ebb94554d",
      },
    };
    axios(getJugador)
      .then((response) => {
        this.isLoaded = true;
        this.jugador = response.data.response[0];
        console.log(this.jugador)
      })
      .catch((error) => {
        console.log(error);
      }); 
  },
};
</script>