<template>
  <div>
    <v-simple-table :search="search">
      <template v-slot:default>
        <thead>
          <tr>
            <th
              v-for="cabecera in cabeceras"
              v-bind:key="cabecera"
              class="m-0 p-0"
            >
              {{ cabecera }}
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="equipo in equipos" v-bind:key="equipo.team.id">
            <td style="width: 10px">
              <div
                v-bind:class="{
                  champions:
                    equipo.description ==
                    'Promotion - Champions League (Group Stage)',
                  'champions-w':
                    equipo.description == 'Promotion - Champions League Women',
                  'champions-w-prev':
                    equipo.description ==
                    'Promotion - Champions League Women (Qualification)',
                  'europa-league':
                    equipo.description ==
                    'Promotion - Europa League (Group Stage)',
                  'europa-league-prev':
                    equipo.description ==
                    'Promotion - Europa Conference League (Qualification)',
                  descenso: equipo.description == 'Relegation',
                  'descenso-l2': equipo.description == 'Relegation - LaLiga2',
                  ascenso: equipo.description == 'Promotion',
                  'asc-play-off': equipo.description == 'Promotion Play-off',
                  posicion: equipo.description == null
                }"
              >
                {{ equipo.rank }}
              </div>
            </td>
            <td>
              <router-link :to="'/equipo/' + equipo.team.id" tag="a">
              <img
                style="vertical-align: middle"
                :src="equipo.team.logo"
                height="20"
              />
              {{ equipo.team.name }}
              </router-link>
            </td>
            <td>{{ equipo.all.played }}</td>
            <td>{{ equipo.all.win }}</td>
            <td>{{ equipo.all.draw }}</td>
            <td>{{ equipo.all.lose }}</td>
            <td class="goles">
              <span>{{ equipo.all.goals.for }}</span> :
              <span>{{ equipo.all.goals.against }}</span>
            </td>
            <td>{{ equipo.points }}</td>
            <td class="forma">
              <div
                v-for="(char, index) in equipo.form"
                :key="index"
                :class="char"
              >
                {{ char | forma }}
              </div>
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>
  </div>
</template>
<script>
export default {
  name: "Clasificacion",
  data() {
    return {
      puntos: 0,
      equipos: [],
      cabeceras: ["Pos", "Equipo", "PJ", "V", "E", "D", "G", "Pts", "Forma"],
    };
  },
  computed: {
    search() {
      var axios = require("axios").default;

      var options = {
        method: "GET",
        url: "https://v3.football.api-sports.io/standings",
        params: { league: this.$route.params.idLiga, season: "2021" },
        headers: {
          "x-rapidapi-host": "v3.football.api-sports.io",
          "x-rapidapi-key": "4ea797df57097c4612ae1e9ebb94554d",
        },
      };

      axios(options)
        .then((response) => {
          this.equipos = response.data.response[0].league.standings[0];
          console.log(response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  filters: {
    forma(valor) {
      valor = valor
        .replaceAll("W", "V")
        .replaceAll("D", "E")
        .replaceAll("L", "D");
      return valor;
    },
  },
};
</script>
<style scoped>
.champions,
.champions-w,
.ascenso {
  background-color: rgb(19, 19, 158);
  color: white;
  text-align: center;
  vertical-align: middle;
  line-height: 25px;
  width: 25px;
  height: 25px;
  border-radius: 5px;
}

.posicion {
  background-color:darkgray;
  color: white;
  text-align: center;
  vertical-align: middle;
  line-height: 25px;
  width: 25px;
  height: 25px;
  border-radius: 5px;
}

.champions-w-prev,
.asc-play-off {
  background-color: rgb(5, 75, 167);
  color: white;
  text-align: center;
  vertical-align: middle;
  line-height: 25px;
  width: 25px;
  height: 25px;
  border-radius: 5px;
}

.europa-league {
  background-color: rgb(133, 2, 2);
  color: white;
  text-align: center;
  vertical-align: middle;
  line-height: 25px;
  width: 25px;
  height: 25px;
  border-radius: 5px;
}

.europa-league-prev {
  background-color: darkgoldenrod;
  color: white;
  text-align: center;
  vertical-align: middle;
  line-height: 25px;
  width: 25px;
  height: 25px;
  border-radius: 5px;
}

.descenso,
.descenso-l2 {
  background-color: red;
  color: white;
  text-align: center;
  vertical-align: middle;
  line-height: 25px;
  width: 25px;
  height: 25px;
  border-radius: 5px;
}

.W {
  float: left;
  background-color: limegreen;
  color: white;
  text-align: center;
  width: 20%;
}
.D {
  float: left;
  background-color: rgb(233, 172, 32);
  color: white;
  text-align: center;
  width: 20px;
  margin: 0.15em;
  border-radius: 5px;
}
.L {
  float: left;
  background-color: red;
  color: white;
  text-align: center;
  width: 20px;
  margin: 0.15em;
  border-radius: 5px;
}
.W {
  background-color: limegreen;
  color: white;
  text-align: center;
  width: 20px;
  margin: 0.15em;
  border-radius: 5px;
}

a {
  text-decoration: none;
}

a:link,
a:visited,
a:active {
  color: black;
}

a:hover {
  color: rgb(75, 130, 233);
}

@media (max-width: 680px) {
  th:last-child {
    display: none;
  }

  th:nth-child(7) {
    display: none;
  }

  .forma {
    display: none;
  }

  .goles {
    display: none;
  }
}

</style>