<template>
  <v-card>
    <Resultado v-bind:partido="partido"></Resultado>
    <v-toolbar color="primary" dark height="40px">
      <v-tabs v-model="tabs" align-with-title>
        <v-tabs-slider color="white"></v-tabs-slider>
        <v-tab v-for="item in items" :key="item">
          {{ item }}
        </v-tab>
      </v-tabs>
    </v-toolbar>
    <v-tabs-items v-model="tabs">
      <v-tab-item v-if="isLoaded">
        <StatsPartido v-bind:partido="partido"></StatsPartido>
      </v-tab-item>
      <v-tab-item v-if="isLoaded">
        <Alineacion v-bind:partido="partido"></Alineacion>
      </v-tab-item>
    </v-tabs-items>
  </v-card>
</template>
<script>
import Resultado from '../components/Resultado.vue'
import StatsPartido from '../components/StatsPartido.vue'
import Alineacion from '../components/Alineacion.vue'
export default {
  components : {Resultado, StatsPartido, Alineacion},
  name: "Partido",
  data() {
    return {
      tabs: null,
      isLoaded : false,
      items: ["Estadísticas", "Alineación"],
      partido: [],
    };
  },
  mounted() {
    var axios = require("axios").default;
    var getPartido = {
      method: "GET",
      url: "https://v3.football.api-sports.io/fixtures",
      params: {
        id: this.$route.params.idpartido,
      },
      headers: {
        "x-rapidapi-host": "v3.football.api-sports.io",
        "x-rapidapi-key": "4ea797df57097c4612ae1e9ebb94554d",
      },
    };
    axios(getPartido)
      .then((response) => {
        this.isLoaded = true
        this.partido = response.data.response[0];
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
</script>