<template>
  <v-simple-table>
    <thead>
      <tr>
        <th v-for="cabecera in cabeceras" v-bind:key="cabecera" class="m-0 p-0">
          {{ cabecera }}
        </th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="temporada, index in estadisticas" v-bind:key="index">
        <td>
          {{temporada.league.season}}
        </td>
        <td>
          <img :src="temporada.league.logo" alt="">
          {{temporada.league.name}}
        </td>
        <td>
          <img :src="temporada.team.logo" alt="">
          {{temporada.team.name}}
        </td>
        <td>
          {{temporada.games.appearences}}
        </td>
        <td>
          {{temporada.games.minutes}}
        </td>
        <td>
          {{temporada.goals.total}}
        </td>
        <td>
          {{temporada.goals.assists}}
        </td>
        <td>
          {{temporada.cards.yellow}}
        </td>
        <td>
          {{temporada.cards.red}}
        </td>
      </tr>
    </tbody>
  </v-simple-table>
</template>
<script>
export default {
  name: "StatsJugador",

  data() {
    return {
      cabeceras: [
        "Temporada",
        "Competición",
        "Equipo",
        "Partidos",
        "Min",
        "Goles",
        "Asistencias",
        "Amarillas",
        "Rojas",
      ],
      estadisticas: [],
    };
  },
  created() {
    var axios = require("axios").default;
    for (let index = 0; index < 10; index++) {
      var getStats = {
        method: "GET",
        url: "https://v3.football.api-sports.io/players",
        params: {
          id: this.$route.params.idJugador,
          season: new Date().getFullYear() - index,
        },
        headers: {
          "x-rapidapi-host": "v3.football.api-sports.io",
          "x-rapidapi-key": "4ea797df57097c4612ae1e9ebb94554d",
        },
      };
      axios(getStats)
        .then((response) => {
          this.estadisticas.push(response.data.response[0].statistics[0]);
          this.estadisticas = this.estadisticas.sort(function(a, b){return ((typeof a.league.season === String) ? a.league.season.substr(0,3) : a.league.season) - ((typeof b.league.season === String) ? b.league.season.substr(0,3) : b.league.season)}).reverse()
        })
        .catch((error) => {
          console.log(error);
        });
    }
  },
  mounted() {
    
  }
};
</script>
<style scoped>
img {
  vertical-align: middle;
  height: 30px;
}
</style>
