import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Liga from '../views/Liga.vue'
import Partido from '../views/Partido.vue'
import Equipo from '../views/Equipo.vue'
import Jugador from '../views/Jugador.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/liga/:idLiga',
    name: 'Liga',
    component: Liga,
  },
  {
    path: '/partido/:idpartido',
    name: 'Partido',
    component: Partido
  }, 
  {
    path: '/equipo/:idEquipo',
    name: 'Equipo',
    component: Equipo
  },
  {
    path: '/jugador/:idJugador',
    name: 'Jugador',
    component: Jugador
  }
]

const router = new VueRouter({
  routes
})

export default router
