<template>
  <div>
    <v-simple-table :search="search">
      <template v-slot:default> 
        <thead>
          <tr>
            <th>Jornada 1</th>
          </tr>
        </thead>
        <tbody v-for="resultado in resultados" v-bind:key="resultado.fixture.id">
          <tr>
            <td>{{resultado.fixture.date.substring(0,16).replace('T', ' ')}}</td>
            <td>
              <img style="vertical-align:middle" :src="resultado.teams.home.logo" height="20">
              {{resultado.teams.home.name}}
            </td>
            <td>
              {{resultado.goals.home}}
            </td>
            <td>-</td>
            <td>
              {{resultado.goals.away}}
            </td>
            <td>
              {{resultado.teams.away.name}}
              <img style="vertical-align:middle" :src="resultado.teams.away.logo" height="20">
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      resultados: [],
      jornada: [],
    };
  },
  computed: {
    search() {
      var axios = require("axios").default;

      var options = {
        method: "GET",
        url: "https://v3.football.api-sports.io/fixtures",
        params: {season: '2021', league: this.$route.params.idLiga, round: this.jornada},
        headers: {
          "x-rapidapi-host": "v3.football.api-sports.io",
          "x-rapidapi-key": "4ea797df57097c4612ae1e9ebb94554d",
        },
      };

      axios(options)
        .then((response) => {
          this.resultados = response.data.response.sort(
            (a,b) =>
              a.fixture.date - b.fixture.date ||
              a.fixture.date.localeCompare(b.fixture.date)
          );
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  mounted() {
      var axios = require("axios").default;

      var options = {
        method: "GET",
        url: "https://v3.football.api-sports.io/fixtures/rounds",
        params: {season: '2021', league: this.$route.params.idLiga},
        headers: {
          "x-rapidapi-host": "v3.football.api-sports.io",
          "x-rapidapi-key": "6a57b52d194e21639390e50c33db91dd",
        },
      };

      axios(options)
        .then((body) => {
          this.jornada = body;
          console.log(this.jornada);
        })
        .catch((error) => {
          console.log(error);
        });
    }
};
</script>
