 <template>
  <div class="container">
    <div
      v-for="(estadisticas, index) in partido.statistics[0].statistics"
      v-bind:key="index"
      class="mt-9"
    >
      <h4>{{ estadisticas.type | formatTipo }}</h4>
      <div class="d-flex justify-center align-center">
        <div class="valor">
          <p>{{ estadisticas.value | formatStat}}</p>
        </div>
        <v-progress-linear
          reverse
          rounded
          height="6"
          class="local"
          :value="valorProgLinear(estadisticas.value, partido.statistics[1].statistics[index].value, estadisticas.type)"
        ></v-progress-linear>
        <v-progress-linear
          rounded
          color="grey"
          height="6"
          class="visitante"
          :value="valorProgLinear(partido.statistics[1].statistics[index].value, estadisticas.value, estadisticas.type)"
        ></v-progress-linear>
        <div class="valor">
          <p>{{ partido.statistics[1].statistics[index].value | formatStat }}</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "StatsPartido",
  props: ["partido"],

  filters: {
    formatStat(valor) {
      if (valor == null) {
        valor = 0;
      }

      return valor.toString().replace("%", "");
    },
    formatTipo(valor) {
      switch (valor) {
        case "Shots on Goal":
          valor = "Tiros a Gol"
          break;
        case "Shots off Goal":
          valor = "Tiros Directamente Fuera"
          break;
        case "Total Shots":
          valor = "Tiros Totales"
          break;
        case "Blocked Shots":
          valor = "Tiros Bloqueados"
          break;
        case "Shots insidebox":
          valor = "Tiros a Puerta"
          break;
        case "Shots outsidebox":
          valor = "Tiros Fuera"
          break;
        case "Fouls":
          valor = "Faltas"
          break;
        case "Corner Kicks":
          valor = "Córners"
          break;
        case "Offsides":
          valor = "Fueras de Juego"
          break;
        case "Ball Possession":
          valor = "% Posesión"
          break;
        case "Yellow Cards":
          valor = "Tarjetas Amarillas"
          break;
        case "Red Cards":
          valor = "Tarjetas Rojas"
          break;
        case "Goalkeeper Saves":
          valor = "Paradas del Portero"
          break;
        case "Total passes":
          valor = "Pases Totales"
          break;
        case "Passes accurate":
          valor = "Pases Precisos"
          break;
        case "Passes %":
          valor = "% Precisión"
          break;
        default:
          break;
      }
      return valor
    }
  },

  methods: {
    valorProgLinear(valLocal, valVisitante, tipoEstadistica){
      if(isNaN(valLocal) && valLocal != null){
        valLocal = parseInt(valLocal, 10)
        valVisitante = parseInt(valVisitante, 10)
      }
      if(tipoEstadistica == "Passes %"){
        return valLocal
      }
      return (valLocal /(valLocal + valVisitante)) * 100
    }
  }
};
</script>
<style scoped>
.local {
  width: 40%;
}

.visitante {
  width: 40%;
}

p {
  margin-top: 0.1em;
  margin-bottom: 0.1em;
  color: darkgrey;
}

h4 {
  text-align: center;
}

.valor{
  text-align: center;
  width: 29px;
  margin-left: 5px;
  margin-right: 5px;
}

.container {
  margin-bottom: 1.5em;
}
</style>